import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'
import axios from 'axios';

   export let getMedia = createAsyncThunk('getMoviesOrTv',async(media)=> {
   let {data} = await axios.get(`https://api.themoviedb.org/3/trending/${media}/week?api_key=bbf4809a83cec3ffbdbc3073a0cc156b`)
   return data.results
   }
   )

let firstReducer= createSlice({
    name:'firstSlice',
    initialState:{
     movies:null,
     tv:null
    },
    reducer:{},
    extraReducers:(builder)=>{
       builder.addCase(getMedia.fulfilled,(state,action)=>
       {
        switch(action.meta.arg){
         case 'movie':
            state.movies = action.payload
            break;
        case 'tv':
            state.tv= action.payload
            break;
        default:
        }
       })
        
    }

   })

   export let action =firstReducer.actions;
   export default firstReducer.reducer