import React, { useEffect} from 'react'
import M from './Home.module.css'
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import { getMedia } from '../Redux/reducer1';
import { useDispatch, useSelector } from 'react-redux';

export default function Home() {

 let {movies}=  useSelector((state)=>state.myAPI)
 let {tv}=  useSelector((state)=>state.myAPI)

 let dispatch= useDispatch();

useEffect(function(){
  dispatch(getMedia("movie"));
  dispatch(getMedia("tv"));
},);

  
  return <>

<Helmet>
  <title>Home</title>
  </Helmet>

  <div className='container my-5'>

<div className='row gy-3'>
 
  <div className='col-md-4 align-self-center'>
    <div className={M.text}><h2 className='p-5'>Trending  Movies To Watch Now <p className="text-secondary fs-6 mt-3">most watched movies by dayes </p></h2></div>
  </div>

{movies?movies.slice(0,10).map((movie,idx)=><div key={idx} className='col-md-2'>
  <Link to={'/details/movie/'+movie.id}>
<div>
  <img alt={idx} src={`https://image.tmdb.org/t/p/w500/${movie.poster_path}`} className='w-100'/>
  <h6 className='text-center mt-3'>{movie.title}</h6>
</div>
</Link>
</div> ):<div className='vh-100 d-flex justify-content-center align-items-center'><i className="fa-solid fa-spinner fa-spin fa-7x text-white"></i></div>}



</div>

<div className='row gy-3 mt-5'>
 
  <div className='col-md-4 align-self-center'>
    <div className={M.text}><h2 className='p-5'>Trending  Tv Shows To Watch Now <p className="text-secondary fs-6 mt-3">most watched movies by dayes </p></h2></div>
  </div>

{tv?tv.slice(0,10).map((tv,idx)=><div key={idx} className='col-md-2'>
<Link to={'/details/tv/'+tv.id}>
<div>
  <img alt={idx} src={`https://image.tmdb.org/t/p/w500/${tv.poster_path}`} className='w-100'/>
  <h6 className='text-center mt-3'>{tv.name}</h6>
</div>
</Link>

</div> ):<div className='vh-100 d-flex justify-content-center align-items-center'><i className="fa-solid fa-spinner fa-spin fa-7x text-white"></i></div>}



</div>

  </div>


  </>
 
}
