import React from 'react'
import { Link, useNavigate } from 'react-router-dom'

export default function Navbar(props) {
  const navigate= useNavigate();

  function getLogout(){
    let x = window.confirm('Do You Wanna Logout?')
    if(x){
      props.removeData();
      navigate('/login')
    }
   
  }
  return <>
        <nav class="navbar navbar-expand-lg navbar-dark mx-3">
  <Link class="navbar-brand" to='/'>Noxe</Link>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>
  <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
  {props.myUser?<div class="navbar-nav">
      <Link class="nav-item nav-link active" to='/'>Home</Link>
      <Link class="nav-item nav-link active" to='/movies'>Movies</Link>
      <Link class="nav-item nav-link active" to='/tv'>Tv Show</Link>

    </div>:''}
    <div class="navbar-nav ms-auto align-items-center">
        <i className= 'fa-brands me-3 fa-facebook-f'></i>
        <i className= 'fa-brands me-3 fa-instagram'></i>
        <i className= 'fa-brands me-3 fa-spotify'></i>
        <i className= 'fa-brands me-3 fa-twitter'></i>
        {props.myUser?<div><span onClick={getLogout} className='p-1 btn btn-outline-primary me-2 t'>{props.myUser.first_name.slice(0,11)}
        </span></div>:<><Link class="nav-item nav-link" to='/register'>Register</Link>
      <Link class="nav-item nav-link" to='/login'>Login</Link></>}
    </div>
  </div>
</nav>
    </>
}
