import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import axios from 'axios';
import { Helmet } from 'react-helmet';

export default function Details() {
    const [details, setDetails] = useState(null);
 
let x = useParams();


 async function getDetails(){
let {data} = await axios.get(`https://api.themoviedb.org/3/${x.media}/${x.id}?api_key=44f1f80237012774946ba3a2fbbce7a1&language=en-US`);
setDetails(data);
 }

 useEffect(function(){
    getDetails();
 })

 return<>

{x.media==='movie'?<Helmet><title>{details?.title}</title></Helmet>:<Helmet><title>{details?.name}</title></Helmet>}
 
 {details?<div className='container my-4'>
<div className='row'>
    <div className='col-lg-4 me-5'>
        <img alt='show' src={`https://image.tmdb.org/t/p/w500/${details.poster_path}`} className='w-100'/>
    </div>
        <div className='col-lg-7 mt-2'>
            {x.media==='movie'?<h2>{details.title}</h2>:<h2>{details.name}</h2>}
<h6 className='text-secondary'>Language: {details.original_language}</h6>
<div className='d-flex flex-wrap align-content-end align-items-center'>
  {details.genres?.map((genre,idx)=> <span className='bg-info px-3 me-3 my-3 py-2 rounded'>{genre.name}</span> )} 
  </div>
    <ul className='lh-lg list-unstyled mt-2'>
        <li>Status : {details.status}</li><li>Vote Count : {details.vote_count}</li><li>Popularity : {details.popularity}</li><li>{details.release_date? "Release Date : " : ""}{details.release_date}</li>
    </ul>
    <p className='text-secondary my-3 fs-6'>{details.overview}</p>
    </div>
</div>
    </div>
:<div className='vh-100 d-flex justify-content-center align-items-center'><i class="fa fa-circle-o-notch fa-2xl"></i></div>}
   
    </>
}
