import axios from 'axios';
import Joi from 'joi';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { Helmet } from 'react-helmet';



export default function Register() {
  
  const Navigate = useNavigate();

  const [ user, setUser ] = useState({
    first_name:'',
    last_name:'',
    age:0,
    email:'',
    password:''
  });

  const [ apiMessages, setApiMessages ] = useState(null)

  const [ errorMessages, setErrorMessages ] = useState(null)

  function getData(e) {
    setErrorMessages(null);
    setApiMessages(null);
    let userValue = e.target.value;
    let userId = e.target.id;
    let newUser = {...user};
    newUser[userId] = userValue;
    setUser(newUser)
  };


  async function getApi(){
  let {data} = await axios.post( 'https://route-movies-api.vercel.app/signup' , user );
  if(data.message==='success'){
    setApiMessages(null);
    Navigate('/login');
  }else{
    setApiMessages(data.message)
  }
 
  }
  function getMyError(label){
    if(errorMessages!=null){
      for(let i=0;i<errorMessages.length;i++){
        if(errorMessages[i].context.label===label){
          return<div className='alert alert-danger'>{errorMessages[i].context.label==='password'?'must contains 3-8 alphanumeric characters':errorMessages[i].message}</div>
        }
      }
    }
  }

  function getError(e) {
    e.preventDefault();

  const schema = Joi.object({
    first_name: Joi.string().alphanum().min(3).max(10).required(),
    last_name:Joi.string().alphanum().min(3).max(10).required(),
    age:Joi.number().min(21).max(81).required(),
    email:Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] }} ).required(),
    password:Joi.string().pattern(/^[a-z0-9]{3,8}$/i).required()
    });
   let errorMessage = schema.validate(user,{abortEarly:false});
    
   if(errorMessage.error===undefined){
      setErrorMessages(null);
      getApi();
    }else{
      let joiResponse = errorMessage.error.details;
      setErrorMessages(joiResponse);
      console.log(errorMessages) 

    }
  };

  return <>

<Helmet>
  <title>Register</title>
  </Helmet>

    <form className='w-50 m-auto' onSubmit={getError}>
      <h2 className='my-4'>Registeration Form</h2>
      <label className='mb-2 ps-1' htmlFor='first_name'>first_name</label>
      <input onChange={getData} className='mb-3 form-control' type='text' id='first_name' placeholder='first_name' />
      {getMyError('first_name')}
      <label className='mb-2 ps-1' htmlFor='last_name'>last_name</label>
      <input onChange={getData} className='mb-3 form-control' type='text' id='last_name' placeholder='last_name' />
       {getMyError('last_name')}
      <label className='mb-2 ps-1' htmlFor='age'>age</label>
      <input onChange={getData} className='mb-3 form-control' type='number' id='age' placeholder='age' />
      {getMyError('age')}
      <label className='mb-2 ps-1' htmlFor='email'>email</label>
      <input onChange={getData} className='mb-3 form-control' type='email' id='email' placeholder='email' />
      {apiMessages?<div className='alert alert-danger'>{apiMessages}</div>:''}
      {getMyError('email')}
      <label className='mb-2 ps-1' htmlFor='password'>password</label>
      <input onChange={getData} className='mb-4 form-control' type='password' id='password' placeholder='password' />
      {getMyError('password')}
      <button className='btn btn-outline-info'>Register</button>
    </form>

  </>

}
