import React, { useEffect, useState } from 'react'
import {createHashRouter,Navigate, RouterProvider } from 'react-router-dom'
import Main from './Components/Main/Main'
import Home from './Components/Home/Home';
import Login from './Components/Login/Login';
import Register from './Components/Register/Register';
import Notfound from './Components/Notfound/Notfound';
import Movies from './Components/Movies/Movies';
import Tv from './Components/Tv/Tv';
import Details from './Components/Details/Details';
import jwtDecode from 'jwt-decode';
import { Offline } from 'react-detect-offline';
import { Provider } from 'react-redux';
import { store } from './Components/Redux/Store'



export default function App() {
  useEffect(() => {
    getUser();
  }, []);
 
  const [myUser, setMyUser] = useState(null);

  function ProtectedRoute(props){
    if(localStorage.getItem('user')==null){
      return <Navigate to='/login'/>
    }else{
      return<>{props.children}</>
    }
  }
  function removeData(){
    localStorage.removeItem('user')
    setMyUser(null)
  }

  function getUser(){
    if(localStorage.getItem('user')!=null){
      let user = localStorage.getItem('user');
      let myUser = jwtDecode(user);
      setMyUser(myUser)
    }
  }

 const router =  createHashRouter([
  {path:'' , element:<Main myUser={myUser} removeData={removeData}/> , children:[
    {path:'' , element:<ProtectedRoute><Home/></ProtectedRoute>},
    {path:'/login' , element:<Login getUser={getUser}/>},
    {path:'/movies' , element:<ProtectedRoute><Movies/></ProtectedRoute>},
    {path:'/tv' , element:<ProtectedRoute><Tv/></ProtectedRoute>},
    {path:'/details' , element:<ProtectedRoute><Details/></ProtectedRoute>,children:[
      {path:':media',children:[
        {path:':id'}
      ] }
    ]},
    {path:'/register' , element:<Register/>},
  ]},
  {path:'/*' , element:<Notfound/>},
 ]
 )
  return <>
  <Offline><div className='bg-dark text-center text-danger fs-1 border border-danger'>No Internet Connection</div></Offline>
   
   <Provider store={store}>   <RouterProvider router={router}/> </Provider>
 
  </>
}
